import React, { useEffect, useState } from "react";

export default function BorrowHistory() {
  const [records, setRecords] = useState([]);
  const [filteredRecords, setFilteredRecords] = useState([]);
  const [filterStatus, setFilterStatus] = useState("all");
  const [filterTime, setFilterTime] = useState("all");
  const [filterDate, setFilterDate] = useState(""); // NEW

  useEffect(() => {
    const isLoggedIn = localStorage.getItem("isLoggedIn");
    if (!isLoggedIn) {
      alert("Vui lng ng nhp  xem lch s mn tr.");
      window.location.href = "/login";
      return;
    }

    // Mock d liu
    const mockData = [
      {
        id: 1,
        title: "D mn phiu lu k",
        start_time: "2025-06-01",
        due_time: "2025-06-15",
        end_time: "2025-06-14",
      },
      {
        id: 2,
        title: "Lp trnh C c bn",
        start_time: "2025-06-20",
        due_time: "2025-07-04",
        end_time: null,
      },
      {
        id: 3,
        title: "T duy nhanh v chm",
        start_time: "2025-04-10",
        due_time: "2025-04-24",
        end_time: "2025-04-23",
      },
      {
        id: 4,
        title: "c nhn tm",
        start_time: "2025-05-02",
        due_time: "2025-05-16",
        end_time: "2025-05-16",
      },
      {
        id: 5,
        title: "Lp trnh Java nng cao",
        start_time: "2025-07-01",
        due_time: "2025-07-15",
        end_time: null,
      },
      {
        id: 6,
        title: "Ti thy hoa vng trn c xanh",
        start_time: "2025-03-12",
        due_time: "2025-03-26",
        end_time: "2025-03-25",
      },
      {
        id: 7,
        title: "Nh gi kim",
        start_time: "2025-01-08",
        due_time: "2025-01-22",
        end_time: "2025-01-20",
      },
      {
        id: 8,
        title: "Sng nh ngi Nht",
        start_time: "2025-06-15",
        due_time: "2025-06-29",
        end_time: null,
      },
      {
        id: 9,
        title: "Clean Code",
        start_time: "2025-07-01",
        due_time: "2025-07-14",
        end_time: null,
      },
      {
        id: 10,
        title: "Git con chim nhi",
        start_time: "2025-02-18",
        due_time: "2025-03-03",
        end_time: "2025-03-01",
      },
      {
        id: 11,
        title: "Nhng ngi khn kh",
        start_time: "2025-06-10",
        due_time: "2025-06-24",
        end_time: "2025-06-23",
      },
    ];

    setRecords(mockData);
    setFilteredRecords(mockData);
  }, []);

  useEffect(() => {
    let filtered = [...records];

    // Lc theo trng thi
    if (filterStatus !== "all") {
      filtered = filtered.filter((r) =>
        filterStatus === "returned" ? r.end_time : !r.end_time
      );
    }

    // Lc theo thi gian (ch khi khng chn ngy c th)
    if (filterTime !== "all" && !filterDate) {
      const now = new Date();
      filtered = filtered.filter((r) => {
        const start = new Date(r.start_time);
        if (filterTime === "month") {
          return (
            start.getMonth() === now.getMonth() &&
            start.getFullYear() === now.getFullYear()
          );
        } else if (filterTime === "year") {
          return start.getFullYear() === now.getFullYear();
        }
        return true;
      });
    }

    // Lc theo ngy c th nu c chn
    if (filterDate) {
      filtered = filtered.filter((r) => r.start_time === filterDate);
    }

    setFilteredRecords(filtered);
  }, [filterStatus, filterTime, filterDate, records]);

  const getStatus = (record) => (record.end_time ? " tr" : "ang mn");

  return (
    <div className="container mt-5">
      <h3 className="text-center mb-4 fw-bold">Lch s mn tr sch</h3>

      {/* B lc */}
      <div className="row g-3 mb-3">
        <div className="col-md-3">
          <select
            className="form-select"
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
          >
            <option value="all">Tt c trng thi</option>
            <option value="returned"> tr</option>
            <option value="borrowing">ang mn</option>
          </select>
        </div>  
        <div className="col-md-3">
          <input
            type="date"
            className="form-control"
            value={filterDate}
            onChange={(e) => setFilterDate(e.target.value)}
          />
        </div>
        <div className="col-md-3">
          <button
            className="btn btn-secondary w-100"
            onClick={() => {
              setFilterDate("");
              setFilterStatus("all");
              setFilterTime("all");
            }}
          >
            Xa b lc
          </button>
        </div>
      </div>

      {/* Bng kt qu */}
      {filteredRecords.length === 0 ? (
        <div className="alert alert-info">
          {records.length === 0
            ? "Bn cha mn sch no."
            : "Khng c kt qu ph hp vi b lc."}
        </div>
      ) : (
        <div className="table-responsive">
          <table className="table table-bordered table-hover">
            <thead className="table-dark">
              <tr>
                <th>STT</th>
                <th>Tn sch</th>
                <th>Ngy mn</th>
                <th>Hn tr</th>
                <th>Ngy tr</th>
                <th>Trng thi</th>
              </tr>
            </thead>
            <tbody>
              {filteredRecords.map((record, index) => (
                <tr key={record.id}>
                  <td>{index + 1}</td>
                  <td>{record.title}</td>
                  <td>{record.start_time}</td>
                  <td>{record.due_time}</td>
                  <td>{record.end_time || "Cha tr"}</td>
                  <td>
                    <span
                      className={`badge ${
                        record.end_time
                          ? "bg-success"
                          : "bg-warning text-dark"
                      }`}
                    >
                      {getStatus(record)}
                    </span>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}
