import React, { useState } from "react";
import { useNavigate, Link } from "react-router-dom";

export default function RegisterForm() {
  const navigate = useNavigate();
  const [form, setForm] = useState({
    name: "", email: "", password: "", confirmPassword: "", phone: ""
  });
  const [errors, setErrors] = useState({});

  const validateEmail = (email) =>
    /^[\w-.]+@[\w-]+\.(edu\.vn)$/.test(email);

  const validateForm = () => {
    const errs = {};
    const { name, email, password, confirmPassword, phone } = form;

    if (!name.trim()) errs.name = "Vui lng nhp tn ng nhp.";
    else if (name.length < 6) errs.name = "Tn ng nhp khng c nh hn 6 k t.";

    if (!email.trim()) errs.email = "Vui lng nhp email.";
    else if (!validateEmail(email)) errs.email = "Email phi c nh dng .edu.vn";

    if (!password) errs.password = "Vui lng nhp mt khu.";
    else if (password.length < 6) errs.password = "Mt khu phi t 6 k t.";

    if (!confirmPassword) errs.confirmPassword = "Vui lng xc nhn mt khu.";
    else if (password !== confirmPassword) errs.confirmPassword = "Mt khu xc nhn khng trng khp.";

    if (!phone.trim()) errs.phone = "Vui lng nhp s in thoi.";

    const existingUsers = JSON.parse(localStorage.getItem("users")) || [];
    if (existingUsers.some(u => u.name === name)) errs.name = "Tn ng nhp  tn ti.";
    if (existingUsers.some(u => u.email === email)) errs.email = "Email  c s dng.";

    return errs;
  };

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleRegister = (e) => {
    e.preventDefault();
    const errs = validateForm();
    if (Object.keys(errs).length) {
      setErrors(errs);
      return;
    }

    const { name, email, password, phone } = form;
    const newUser = {
      name, email, password, phone,
      school_name: "", //  trng ban u
      address: "",
      status: "active", role: "reader"
    };

    const users = JSON.parse(localStorage.getItem("users")) || [];
    users.push(newUser);
    localStorage.setItem("users", JSON.stringify(users));
    alert("ng k thnh cng!");
    navigate("/login");
  };

  return (
    <div className="container-fluid min-vh-100 d-flex align-items-center justify-content-center bg-light">
      <div className="row shadow rounded overflow-hidden" style={{ maxWidth: "900px", width: "100%" }}>
        <div className="col-md-6 bg-white p-5">
          <h3 className="text-center mb-4 text-danger">ng k</h3>

          <div className="d-flex justify-content-center gap-3 mb-3">
            {["", "K", "T", "K"].map((c, i) => (
              <button key={i} className="btn btn-outline-danger rounded-circle">{c}</button>
            ))}
          </div>

          <p className="text-center text-muted">in y  thng tin bn di</p>

          <form onSubmit={handleRegister} noValidate>
            {[
              { name: "name", type: "text", placeholder: "Tn ng nhp" },
              { name: "phone", type: "text", placeholder: "S in thoi" },
              { name: "email", type: "email", placeholder: "Email .edu.vn" },
              { name: "password", type: "password", placeholder: "Mt khu" },
              { name: "confirmPassword", type: "password", placeholder: "Xc nhn mt khu" }
            ].map(({ name, type, placeholder }) => (
              <div className="mb-3" key={name}>
                <input
                  type={type}
                  name={name}
                  className={`form-control ${errors[name] ? "is-invalid" : ""}`}
                  placeholder={placeholder}
                  value={form[name]}
                  onChange={handleChange}
                />
                {errors[name] && <div className="invalid-feedback">{errors[name]}</div>}
              </div>
            ))}

            <div className="form-check mb-3">
              <input className="form-check-input" type="checkbox" required id="agree" />
              <label className="form-check-label" htmlFor="agree">
                Ti ng  vi iu khon s dng
              </label>
            </div>

            <button type="submit" className="btn btn-danger w-100">ng k</button>
            <p className="text-center mt-3">
               c ti khon? <Link to="/login" className="text-decoration-none">ng nhp</Link>
            </p>
          </form>
        </div>

        <div className="col-md-6 p-0 d-none d-md-block">
          <img
            src="https://anybooks.vn/uploads/images/sach-999-la-thu-gui-cho-chinh-minh.jpeg"
            alt="Register"
            className="img-fluid h-100 w-100"
            style={{ objectFit: "cover" }}
          />
        </div>
      </div>
    </div>
  );
}
