import { useParams, Link } from "react-router-dom";
import books from "../data/books";

export default function BookDetail() {
  const { id } = useParams();
  const book = books.find(b => b.id.toString() === id);

  if (!book) {
    return (
      <div className="container py-5 text-center">
        <h2 className="text-danger"> Khng tm thy sch</h2>
        <Link to="/" className="btn btn-outline-primary mt-3"> V trang ch</Link>
      </div>
    );
  }

  const isLoggedIn = localStorage.getItem("isLoggedIn") === "true";
  const isAvailable = book.book_copies?.length > 0;
  const authors = book.authors?.map(a => a.name).join(", ") || "Khng r";
  const category = book.category?.name || "Cha phn loi";

  const handleAction = (type) => {
    if (!isLoggedIn) {
      alert(` Bn phi ng nhp  ${type === "borrow" ? "mn" : "t trc"} sch.`);
      return;
    }

    const current = localStorage.getItem("currentBorrowedBook");
    if (current && current !== book.id.toString()) {
      alert(" Bn ch c mn hoc t trc 1 cun sch ti mt thi im.");
      return;
    }

    localStorage.setItem("currentBorrowedBook", book.id.toString());
    alert(type === "borrow"
      ? "  gi yu cu mn sch!"
      : "  gi yu cu t sch!");
  };

  return (
    <div className="container py-5">
      {/* Hnh nh v thng tin chnh */}
      <div className="row align-items-center">
        <div className="col-md-4 mb-4">
          <img src={book.image} alt={book.title} className="img-fluid rounded shadow" />
        </div>

        <div className="col-md-8">
          <h2 className="fw-bold text-primary mb-3">{book.title}</h2>
          <ul className="list-unstyled text-muted fs-6">
            <li><strong>M sch:</strong> {book.id}</li>
            <li><strong>Tc gi:</strong> <span className="text-success">{authors}</span></li>
            <li><strong>Th loi:</strong> {category}</li>
            <li><strong>NXB:</strong> {book.publisher || "?"}</li>
            <li><strong>Nm:</strong> {book.year || "?"}</li>
            <li><strong>Lt xem:</strong> {book.views || 0}</li>
            <li><strong>S lng cn:</strong> {book.book_copies?.length || 0}</li>
          </ul>

          {/* Nt mn hoc t trc */}
          <div className="mt-4">
            {isAvailable ? (
              <>
                <div className="alert alert-success mb-3">
                   Sch hin c sn. Mi bn n th vin  mn.
                </div>
                <button className="btn btn-primary" onClick={() => handleAction("borrow")}>
                  Mn Sch Ngay
                </button>
              </>
            ) : (
              <>
                <div className="alert alert-warning mb-3">
                   Sch  ht. Bn c th t trc.
                </div>
                <button className="btn btn-outline-danger" onClick={() => handleAction("reserve")}>
                  t Trc
                </button>
              </>
            )}
          </div>
        </div>
      </div>

      {/* Phn gii thiu */}
      <div className="mt-5 pt-4 border-top">
        <h5 className="text-muted mb-3">Gii Thiu Sch</h5>
        <h6 className="text-danger">{book.title?.toUpperCase()}</h6>
        <p style={{ textAlign: "justify" }}>
          {book.description || `Cun sch "${book.title}" mang n nhiu cm xc v gi tr sng. Hy n c  khm ph nhng iu tuyt vi t tc phm ny.`}
        </p>
      </div>

      {/* Nt quay li */}
      <div className="mt-4">
        <Link to="/" className="btn btn-outline-dark"> Quay li danh sch</Link>
      </div>
    </div>
  );
}
