import React, { useState, useEffect } from "react";
import "./style.css";

export default function ProfilePage() {
  const [user, setUser] = useState({
    name: "admin",
    email: "admin@student.edu.com",
    phone: "0899804328",
    address: "bnh yn nam thanh nam trc",
    avatar:
      "https://yt3.ggpht.com/yti/ANjgQV-FgWf4XF8YlaoUDJNhBbH7KQ8nK9jSlWtuRle6_trGSaY=s88-c-k-c0x00ffffff-no-rj-mo",
    password: "",
    newPassword: "",
    confirmPassword: "",
  });
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const validateInfo = () => {
    const errs = {};
    if (!user.name.trim()) errs.name = "Vui lng nhp tn ng nhp";
    if (!user.phone.trim()) errs.phone = "Vui lng nhp s in thoi";
    if (!user.address.trim()) errs.address = "Vui lng nhp a ch";
    return errs;
  };

  const validatePassword = () => {
    const errs = {};
    if (!user.password) errs.password = "Vui lng nhp mt khu hin ti";
    if (!user.newPassword) errs.newPassword = "Vui lng nhp mt khu mi";
    else if (user.newPassword.length < 6)
      errs.newPassword = "Mt khu phi c t nht 6 k t";
    if (user.newPassword !== user.confirmPassword)
      errs.confirmPassword = "Xc nhn mt khu khng trng khp";
    return errs;
  };

  const handleSaveInfo = () => {
    const errs = validateInfo();
    if (Object.keys(errs).length > 0) {
      setErrors(errs);
    } else {
      alert("Cp nht thng tin thnh cng!");
      setErrors({});
    }
  };

  const handleUpdatePassword = () => {
    const errs = validatePassword();
    if (Object.keys(errs).length > 0) {
      setErrors(errs);
    } else {
      alert("Cp nht mt khu thnh cng!");
      setErrors({});
    }
  };

  return (
    <div className="container-fluid bg-light min-vh-100 py-5">
      <div className="container">
        <h3 className="mb-4 fw-bold text-center">Trang c nhn</h3>
        <div className="row">
          <div className="col-md-10 col-lg-8 mx-auto">
            <div className="card p-4 shadow-sm mb-4">
              <h5 className="mb-3 fw-bold">Thng tin ti khon</h5>

              <div className="row">
                <div className="col-md-3 text-center">
                  <img
                    src={user.avatar}
                    alt="avatar"
                    className="rounded border mb-2"
                    style={{
                      width: "100%",
                      maxWidth: "150px",
                      height: "150px",
                      objectFit: "cover",
                    }}
                  />
                  <label className="btn btn-light btn-sm border">
                     Thay i nh i din
                    <input
                      type="file"
                      accept="image/*"
                      onChange={(e) => {
                        const file = e.target.files[0];
                        if (file) {
                          const reader = new FileReader();
                          reader.onload = () => {
                            setUser({ ...user, avatar: reader.result });
                          };
                          reader.readAsDataURL(file);
                        }
                      }}
                      hidden
                    />
                  </label>
                </div>

                <div className="col-md-9">
                  <div className="row g-3">
                    <div className="col-md-6">
                      <label className="form-label">H v tn *</label>
                      <input
                        className={`form-control ${errors.name ? "is-invalid" : ""}`}
                        name="name"
                        value={user.name}
                        onChange={handleChange}
                      />
                      {errors.name && <div className="invalid-feedback">{errors.name}</div>}
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">Email *</label>
                      <input className="form-control" value={user.email} disabled />
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">S in thoi *</label>
                      <input
                        className={`form-control ${errors.phone ? "is-invalid" : ""}`}
                        name="phone"
                        value={user.phone}
                        onChange={handleChange}
                      />
                      {errors.phone && <div className="invalid-feedback">{errors.phone}</div>}
                    </div>
                    <div className="col-md-6">
                      <label className="form-label">a ch *</label>
                      <input
                        className={`form-control ${errors.address ? "is-invalid" : ""}`}
                        name="address"
                        value={user.address}
                        onChange={handleChange}
                      />
                      {errors.address && <div className="invalid-feedback">{errors.address}</div>}
                    </div>
                    <div className="col-12">
                      <button className="btn btn-primary mt-3" onClick={handleSaveInfo}>
                        Lu thay i
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* i mt khu */}
            <div className="card p-4 shadow-sm">
              <h5 className="fw-bold mb-3">Thay i mt khu</h5>
              <div className="row g-3">
                <div className="col-md-4">
                  <label className="form-label">Mt khu hin ti *</label>
                  <input
                    type="password"
                    className={`form-control ${errors.password ? "is-invalid" : ""}`}
                    name="password"
                    value={user.password}
                    onChange={handleChange}
                  />
                  {errors.password && <div className="invalid-feedback">{errors.password}</div>}
                </div>
                <div className="col-md-4">
                  <label className="form-label">Mt khu mi *</label>
                  <input
                    type="password"
                    className={`form-control ${errors.newPassword ? "is-invalid" : ""}`}
                    name="newPassword"
                    value={user.newPassword}
                    onChange={handleChange}
                  />
                  {errors.newPassword && (
                    <div className="invalid-feedback">{errors.newPassword}</div>
                  )}
                </div>
                <div className="col-md-4">
                  <label className="form-label">Xc nhn mt khu *</label>
                  <input
                    type="password"
                    className={`form-control ${errors.confirmPassword ? "is-invalid" : ""}`}
                    name="confirmPassword"
                    value={user.confirmPassword}
                    onChange={handleChange}
                  />
                  {errors.confirmPassword && (
                    <div className="invalid-feedback">{errors.confirmPassword}</div>
                  )}
                </div>
                <div className="col-12">
                  <button className="btn btn-primary mt-3" onClick={handleUpdatePassword}>
                    Cp nht mt khu
                  </button>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  );
}
