import { Link } from "react-router-dom";

export default function BookCard({ book }) {
  const isLoggedIn = localStorage.getItem("isLoggedIn") === "true";

  // Tc gi, th loi
  const authors = book.authors?.map(author => author.name).join(", ") || "Khng r";
  const category = book.category?.name || "Cha phn loi";

  //  Tnh s lng sch cn li (status === "available")
  const availableCount = Array.isArray(book.book_copies)
    ? book.book_copies.filter(copy => copy.status === "available").length
    : 0;

  const isAvailable = availableCount > 0;

  const handleBorrow = (e) => {
    e.preventDefault();
    alert(isLoggedIn ? " Yu cu mn sch  c gi n th th!" : " Bn phi ng nhp  mn sch.");
  };

  return (
    <Link to={`/book/${book.id}`} className="text-decoration-none text-dark">
      <div className="card h-100 shadow-sm border-0 rounded-4 overflow-hidden book-card">
        {/* Hnh nh sch */}
        <div className="position-relative" style={{ height: "220px", overflow: "hidden" }}>
          <img
            src={book.image}
            alt={book.title}
            className="w-100 h-100 object-fit-cover"
          />
        </div>

        {/* Ni dung */}
        <div className="card-body d-flex flex-column bg-light-subtle">
          <h5 className="card-title fw-semibold text-primary-emphasis">{book.title}</h5>
          <ul className="list-unstyled small text-secondary mb-3">
            <li><strong>M sch:</strong> {book.id}</li>
            <li><strong>Tc gi:</strong> {authors}</li>
            <li><strong>Th loi:</strong> {category}</li>
            <li><strong>NXB:</strong> {book.publisher || "Khng r"}</li>
            <li><strong>Nm:</strong> {book.year || "?"}</li>
            <li><strong>S lng cn:</strong> {availableCount > 0 ? availableCount : "Ht sch"}</li>
          </ul>

          <div className="mt-auto d-flex justify-content-between align-items-center">
            <button className="btn btn-sm btn-outline-success rounded-pill px-3">Xem thm</button>
            <button
              className={`btn btn-sm rounded-pill px-3 ${isAvailable ? "btn-success" : "btn-outline-secondary"}`}
              onClick={isAvailable ? handleBorrow : undefined}
              disabled={!isAvailable}
            >
              {isAvailable ? "Mn Sch" : "Ht Sch"}
            </button>
          </div>
        </div>
      </div>
    </Link>
  );
}
