import React, { useState } from "react";
import { useNavigate } from "react-router-dom";

export default function ChangePassword() {
  const navigate = useNavigate();
  const [form, setForm] = useState({ oldPassword: "", newPassword: "", confirmPassword: "" });
  const [error, setError] = useState({});
  const [success, setSuccess] = useState("");

  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
    setError({});
    setSuccess("");
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { oldPassword, newPassword, confirmPassword } = form;
    const currentPassword = localStorage.getItem("role") === "librarian" ? "admin" : "user";

    const errors = {
      ...(oldPassword !== currentPassword && { oldPassword: "Mt khu hin ti khng ng." }),
      ...(!oldPassword && { oldPassword: "Vui lng nhp mt khu hin ti." }),
      ...(!newPassword && { newPassword: "Vui lng nhp mt khu mi." }),
      ...(newPassword && newPassword.length < 6 && { newPassword: "Mt khu mi phi t 6 k t." }),
      ...(!confirmPassword && { confirmPassword: "Vui lng xc nhn mt khu." }),
      ...(newPassword !== confirmPassword && { confirmPassword: "Mt khu xc nhn khng khp." }),
    };

    if (Object.keys(errors).length) return setError(errors);

    setSuccess("i mt khu thnh cng!");
    setTimeout(() => navigate("/profile"), 1500);
  };

  return (
    <div className="container-fluid min-vh-100 d-flex align-items-center justify-content-center bg-light">
      <div className="bg-white shadow rounded p-5" style={{ maxWidth: 500, width: "100%" }}>
        <h3 className="text-center text-danger mb-4">i mt khu</h3>
        {success && <div className="alert alert-success">{success}</div>}
        <form onSubmit={handleSubmit}>
          {[
            { name: "oldPassword", placeholder: "Mt khu hin ti" },
            { name: "newPassword", placeholder: "Mt khu mi" },
            { name: "confirmPassword", placeholder: "Xc nhn mt khu" },
          ].map(({ name, placeholder }) => (
            <div className="mb-3" key={name}>
              <input
                type="password"
                name={name}
                placeholder={placeholder}
                className={`form-control ${error[name] ? "is-invalid" : ""}`}
                value={form[name]}
                onChange={handleChange}
              />
              {error[name] && <div className="invalid-feedback">{error[name]}</div>}
            </div>
          ))}
          <button type="submit" className="btn btn-danger w-100">Xc nhn i mt khu</button>
        </form>
      </div>
    </div>
  );
}
